<template>
  <div class="home text-center">
    <img alt="Vue logo" src="../assets/logo.png" />

    <div>Current Locale : {{ locale }}</div>
    <div>Test Vuetify Built-in Msg : {{ $vuetify.lang.t('$vuetify.close') }}</div>
    <div>Test Custom Msg : {{ $vuetify.lang.t('$vuetify.test') }}</div>
    <v-select
      :items="langs"
      label="Lang"
      dense
      solo
      :menu-props="{ offsetY: true }"
      v-model="locale"
    ></v-select>
    <v-card class="pa-4">
      <line-chart :chartData="chartData"></line-chart>
      <v-btn v-gradient="{ type:'linear', deg:'30', color1: 'green' }" @click="randChart()">Random</v-btn>
    </v-card>
    <v-card class="pa-4">
      <bar-chart :chartData="chartData"></bar-chart>
      <v-btn v-gradient="{ type:'linear', deg:'30', color1: 'green' }" @click="randChart()">Random</v-btn>
    </v-card>
    <v-card class="pa-4">
      <radar-chart :chartData="chartData"></radar-chart>
      <v-btn v-gradient="{ type:'linear', deg:'30', color1: 'green' }" @click="randChart()">Random</v-btn>
    </v-card>
    <v-card class="pa-4">
      <pie-chart :chartData="chartData"></pie-chart>
      <v-btn v-gradient="{ type:'linear', deg:'30', color1: 'green' }" @click="randChart()">Random</v-btn>
    </v-card>
    <v-card class="pa-4">
      <polar-area :chartData="chartData"></polar-area>
      <v-btn v-gradient="{ type:'linear', deg:'30', color1: 'green' }" @click="randChart()">Random</v-btn>
    </v-card>
  </div>
</template>

<script>
// @ is an alias to /src
import langMixin from "../lang/mixin";
import LineChart from "../components/Chart/LineChart";
import BarChart from "../components/Chart/BarChart";
import RadarChart from "../components/Chart/RadarChart";
import PieChart from "../components/Chart/PieChart";
import PolarArea from "../components/Chart/PolarArea";
export default {
  name: "Home",
  mixins: [langMixin],
  components: {
    LineChart,
    BarChart,
    RadarChart,
    PieChart,
    PolarArea,
  },
  data() {
    return {
      chartData: {},
    };
  },
  computed: {
    locale: {
      get() {
        return this.$vuetify.lang.current;
      },
      set(val) {
        this.$vuetify.lang.current = val;
      },
    },
  },
  methods: {
    getRandomInt() {
      return Math.floor(Math.random() * (50 - 5 + 1)) + 5;
    },
    randChart() {
      const size = 20;
      this.chartData = {
        labels: [...Array.from(Array(size), (_, i) => `Data ${i + 1}`)],
        datasets: [
          {
            label: "Data One",
            backgroundColor: "#556677",
            data: [...Array.from(Array(size), () => this.getRandomInt())],
          },
          {
            label: "Data Two",
            backgroundColor: "#f87979",
            data: [...Array.from(Array(size), () => this.getRandomInt())],
          },
        ],
      };
    },
  },
  mounted() {
    setInterval(() => {
      this.randChart();
    }, 1500);
  },
};
</script>
